
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 100px;
}
h5 {
  color: #cccccc;
}

.wrapper {
  margin: 0 auto;
  width: 500px;
  padding: 20px;
  background-color: #f00;
  border-radius: 10px;
  -webkit-box-shadow: 0 10px 12px rgb(105, 53, 53);
          box-shadow: 0 10px 12px rgb(105, 53, 53);
}
.btn {
  background-color:#1AAD19;
  border: none;
  color: #ffffff;
  height: 30px;
  width: 60px
}
.btn:hover {
  background-color:rgb(151, 91, 207);
}
.input {
  padding: 0 0 0 10px;
  border: none;
  height: 30px;
  width: 300px;
}
.select {
  padding: 0 10px;
  border: none;
  height: 30px;
  width: 100px;
}
@media screen and (max-width: 414px) {
.btn{width: 100%
}
.select{width: 100%
}
.input{width: 97%; display: block;
}
.wrapper{width: 305px;
}
}

#translateOutput {
  margin-top: 30px;
  color: aqua;
  font-size: 40px;
}
